.base-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  
    .header {
      font-size: 24px;
      font-family: "Open Sans", sans-serif;
    }
  
    .content {
      display: flex;
      flex-direction: column;
  
      .logo {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        h1{
            flex: 1;
            flex-grow: 4;
            padding-bottom: 0;
            margin-bottom: 0;
        }
        p{
            padding-top: 0;
            margin-top: 0;
        }
      }
  
      .form {
        margin-top: 2em;
        display: flex;
        flex-direction: column;
        align-items: center;
  
        .form-group {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          width: fit-content;
          label {
            font-size: 20px;
          }
          input {
            margin-top: 6px;
            min-width: 18em;
            height: 37px;
            padding: 0px 10px;
            font-size: 16px;
            font-family: "Open Sans", sans-serif;
            background-color: #f3f3f3;
            border: 0;
            border-radius: 4px;
            margin-bottom: 31px;
            transition: all 250ms ease-in-out;
            &:hover {
              background-color: #ffffff;
              box-shadow: 0px 0px 14px 0.3px rgba(56, 56, 56, 0.588)
            }
  
            &:focus {
              outline: none;
              box-shadow: 0px 0px 12px 0.8px #727272;
            }
          }
        }
      }
    }
  
    .footer {
      margin-top: 3em;
    }
  }